<template>
  <div>
    <el-card class="box-card">
      <el-row>
        <b>姓名：</b>
        <el-input
          v-model="queryParam.name"
          placeholder="请输入姓名"
          style="width: 150px"
        ></el-input>
        <b>类型：</b>
        <el-select v-model="queryParam.type" style="width: 150px"  placeholder="请选择类型">
          <el-option
            v-for="key in Object.keys(typeOptions)"
            :key="key"
            :label="typeOptions[key]"
            :value="key"
          >
          </el-option>
        </el-select>
        <el-button type="primary" @click="pageNo = 1; pages();" icon="el-icon-search">搜索</el-button>
        <el-button type="primary" @click="reset" icon="el-icon-magic-stick">重置</el-button>
      </el-row>

      <template>
        <el-table
          :data="tableData"
          border
          style="width: 100%"
          max-height="500"
        >
          <el-table-column type="index" width="55" />
          <el-table-column prop="name" label="姓名" />
          <el-table-column prop="type" label="类型" :formatter="row => typeOptions[row.type]" />
          <el-table-column prop="telephone" label="电话" />
        </el-table>

        <div class="block">
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="currentPage"
            :page-sizes="pagesizes"
            :page-size="pagesize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="total"
          >
          </el-pagination>
        </div> </template
    ></el-card>
  </div>
</template>
</el-card>

    
  </div>
</template>

<script>
import API from "../../utils/service";
export default {
  data() {
    return {
      name: '通讯录',
      tableData: [],
      queryParam: {
        name: undefined,
        type: undefined
      },
      typeOptions: {
        TEACHER: "教师",
        ADMIN: "管理员"
      },
      currentPage: 1,
      total: 0,
      pagesize: 5,
      pagesizes: [3, 4, 5, 6],
    };
  },
  mounted() {
    this.$bus.$emit('receiveParams',this.name)
    this.pages(1, this.pagesize);
  },
  methods: {
    handleCurrentChange(val) {
      this.currentPage = val;
      this.pages(val, this.pagesize);
    },
    handleSizeChange(val) {
      this.pagesize = val;
      this.pages(this.currentPage, this.pagesize);
    },
    pages() {
      API({
        url: "/teacherController/mail/list",
        params: {
          pageNo: this.currentPage,
          pageSize: this.pagesize,
          ...this.queryParam
        },
        method: "get"
      }).then((response) => {
        this.queryParam.name = ''
        this.total = response.data.data.total;
        this.tableData = response.data.data.records;
      });
    },
    reset() {
      this.queryParam = {};
      this.pageNo = 1;
      this.pages();
    }
  }
};
</script>

<style lang="scss" scoped>
.el-row {
  margin: 30px 0 50px 0;
}
.text {
  font-size: 20px;
}

.box-card {
  width: 100%;
}

.el-card {
  padding: 0px;
}

.el-button--primary {
  background-color: #fab6b6;
  border-color: #fab6b6;
}

.el-button--primary:focus,
.el-button--primary:hover {
  background: #e9baba;
  border-color: #f3dfdf;
  color: #fff;
}

::v-deep .el-pagination.is-background .el-pager li:not(.disabled).active {
  background-color: #f3dfdf;
}
</style>